.App {
  text-align: center;
  display: flex;
  align-items: center;
  /* justify-content: center; */
  width: 100%;
  height: 100vh;
  flex-direction: column;
  padding-top: 200px;
  /* padding: 200px 200px 200px 0; */
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.submit {
  position: absolute;
  margin-top: 50px;
  bottom: 400px;
}

.steps {
  margin-bottom: 200px;
  width: 600px;
}

.uploadPic {
  margin-bottom: 30px;
}